.login{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(9, 53, 69, 1);
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: content-box;
}

.login-bottom{
    position: absolute;
    width: 100%;
    bottom: 0;
}

.head1{
    color: white;
    font-size: 4rem;
    font-family: 'Lexend Deca', sans-serif;
    margin-top: 50px;
}

.head2{
    color: white;
    font-family: 'lexend Deca' sans-serif;
    font-size: 1.3rem;
    margin-top: 30px;
}

.inpu{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 35px;
    margin-top: 40px;
    font-family: 'lexend Deca' sans-serif;
}

.inpu>input{
  width: 300px;
  height: 45px;
  border-radius: 10px;
  outline: none;
  border: none;
  background: #224957;
  color: white;
  padding: 5px 0 5px 10px;
  box-sizing:border-box;
  transition: 1s;
  font-size: 1rem;
}


.inpu>button{
    cursor: pointer;
    width: 300px;
    height: 45px;
    padding: 5px;
    border: none;
    outline: none;
    border-radius: 10px;
    background: #20DF7F;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.30);
    font-size: 1rem;
    color: white;
    box-sizing:border-box;
    transition: 1s;
    transform: translateY(-10px);
}

.inpu>button:hover{
    transform: scale(1.01);
    transform: translatey(-14px);
}

.input{
    position: relative;
    display: flex;
    align-items: center;
}

.input>input{
    width: 300px;
    height: 45px;
    border-radius: 10px;
    outline: none;
    border: none;
    background: #224957;
    color: white;
    padding: 5px 40px 5px 10px;
    box-sizing:border-box;
    transition: 1s;
    font-size: 1rem;
}



.input>p{
    position: absolute;
    transform: translatex(270px); 
    transition: 1s;
}

.icons{
    cursor: pointer;
}

.switch{
    padding: 0;
    color: white;
    padding: 5px 0 0 6px;
    margin-top: 100px;
    font-size: 1.1rem;
    transition: 1s;
    border: none;
    outline: none;
    text-decoration: none;
}

.switch:hover{
    transform: scale(1.03);
    color: green;
    cursor: pointer;
}
@media (max-width:425px){
    .login-bottom{
        display: none;
    }
    .head1{
        font-size: 3rem;
    }
    .head2{
        font-size: 1rem;
    }
    .inpu>input{
        width: 250px;
        height: 40px;
    }
    .input>input{
        width: 250px;
        height: 40px;
    }
    .input>p{
        transform: translateX(215px);
    }
    .inpu>button{
        width: 250px;
        height: 40px;
    }
    
}